import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { routes } from './app.router';
import {  AngularFireModule} from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';



import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { MainPage } from '../pages/FirstPage/firstpage';
import { SigninPage } from '../pages/Signin/signin';
import { SignupPage } from '../pages/signup/signup';

var config = {
    apiKey: "AIzaSyAREdPE_8pxyMf5852NL622vobIBR-Enbw",
    authDomain: "xperia-94cd1.firebaseapp.com",
    databaseURL: "https://xperia-94cd1.firebaseio.com",
    projectId: "xperia-94cd1",
    storageBucket: "xperia-94cd1.appspot.com",
    messagingSenderId: "743628871051"
  };



@NgModule({
  declarations: [
    MyApp,
    HomePage,
    MainPage,
    SigninPage,
    SignupPage
  ],
  imports: [
    BrowserModule,
    routes,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(config),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MainPage,
    SigninPage,
    SignupPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



